.edapage-background {
  background-color: #eee;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.edapage-container {
  width: 80vw;
  height: 90vh;
  border-radius: 10px;
  background-color: white;
  border: 0;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding: 10px;
  display: block;
}

.remote-controller {
  text-align: center;
  color: white;
  text-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  width: 15vw;
  min-height: 90vh;
  border-radius: 10px;
  background-color: #675EC2;
  padding: 10px;
  display: block;
}

.changing-btn {
  width: 80%;
  height: 30px;
  border-radius: 10px;
  color: white;
  background-color: black;
  padding: 5px;
  margin: 5px;
}/*# sourceMappingURL=App.css.map */